import React from "react";
import { connect } from "react-redux";
import { Auth, FC_DistributedVacantPosts } from "../../actions";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import Loading from "../../components/Loading/Loading";
import { StoreState } from "../../reducers";
// import Alert, { AlertInterface } from "../../components/Alert/Alert";

// props for the component
interface AppProps {
  auth: Auth;
}

interface AppState {
  loading: boolean;
}

class _DistributedVaccantReport extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: true,
    };
  }

  componentDidMount() {
    FC_DistributedVacantPosts("1", (status, data, msg) => {
      this.setState({ loading: false });
    });
  }

  render() {
    if (this.state.loading === true)
      return (
        <div className="py-8 text-center">
          <Loading className="mx-auto" />
        </div>
      );
    return <DashboardContainer>Distributed</DashboardContainer>;
  }
}

const mapStateToProps = ({ auth }: StoreState): { auth: Auth } => {
  return { auth: auth };
};

export const DistributedVaccantReport = connect(
  mapStateToProps,
  {}
)(_DistributedVaccantReport);
